.Overview{
    width: 1122px;
    height: 134px;
    margin-top: 50px;
    margin-left: 10px;
    display: flex;
    justify-content: space-between;


    .text{
        width: 258px;
        height: 134px;
        border-radius: 10px ;
        background-color: #F7F8FC;
        border: 2px solid #F7F8FC;


        p{
            text-align: center;
            color: #9FA2B4;
            font-size: 19px;
            padding-top: 20px;
           
        }
        h1{
            text-align: center;
            font-size: 40px;
        }
        &:hover{
            border: 2px solid #3751FF;
            color: #3751FF;

        }
    }

    
}
