.Tickets{
    width: 1122px;
    height: 740px;
    border: 1px solid rgb(205, 201, 201);
    margin-top: 50px;
    margin-left: 35px;
    display: block;
    .text{
        width: 1120px;
        height: 92px;
        border: 1px solid rgb(205, 201, 201);
        display: flex;
        justify-content: space-between;
        .email{
            h3{
                margin-left: 20px;
                margin-top: 20px;
                font-size: 16px;
            }
            p{
                margin-left: 20px;
                font-size: 14px;
                color: #C5C7CD;
            }
        }
        .year{

            h3{
                margin-top: 20px;
                font-size: 16px;
            }
            p{
                font-size: 14px;
                color: #C5C7CD;
            }
        }
        .data{

            h3{
                margin-top: 20px;
                font-size: 16px;
            }
            p{
                font-size: 14px;
                color: #C5C7CD;
            }
        }
        .priority{
            button{
                width: 54px;
                height: 24px;
                margin-top: 25px;
                margin-right: 40px;
                border-radius: 20px;
                background-color: #F12B2C;
                border: 2px solid #F12B2C;
                color: white;
            }
            .btn{
                border: 2px solid #FEC400;
                background-color: #FEC400;

            }
            .btn2{
                border: 2px solid  #29CC97;
                background-color: #29CC97;
            }
        }



    }
}